import { Injectable, Inject } from '@angular/core';
import {HttpClient, HttpResponse, HttpErrorResponse} from '@angular/common/http'
import { Customer } from 'app/Models/customer.model';
import { Vehicle } from 'app/Models/vehicle.model';

@Injectable({
  providedIn: 'root'
})
export class CustomerService {

  HostUrl: string;
  public IsLoggedIn: boolean;
  public SelectctedCustomer: Customer = { } as any;

  constructor(private http: HttpClient, @Inject('BASE_URL') baseUrl: string) {
    this.HostUrl=baseUrl;

   }


   async GetAllCustomers() {
    let customerList: Customer[] ;
    await this.http.get(  this.HostUrl + 'api/customer/GetAllCustomers')   .toPromise()
    .then(res => customerList = res as Customer[])
    return customerList;
  }

  AddUpdateCustomer(cModel: Customer) {
    return this.http.post(this.HostUrl + 'api/customer/AddUpdateCustomer', cModel);
  }

  async GetVehicleList(customerID: number) {
    let vehicleList: Vehicle[] ;
    await this.http.get(  this.HostUrl + 'api/customer/GetVehicleList/' + customerID)   .toPromise()
    .then(res => vehicleList = res as Vehicle[])
    return vehicleList;
  }

  AddUpdateVehicle(vModel: Vehicle) {
    return this.http.post(this.HostUrl + 'api/customer/AddUpdateVehicle', vModel);
  }

  DeleteVehicle(id: number) {
    
    return this.http.get(this.HostUrl + 'api/customer/DeleteVehicle/' + id);
  }


  DeleteCustomer(id: number) {
    
    return this.http.get(this.HostUrl + 'api/customer/DeleteCustomer/' + id);
  }
}
