import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

declare const $: any;
declare interface RouteInfo {
    path: string;
    title: string;
    icon: string;
    class: string;
}
export const ROUTES: RouteInfo[] = [
    { path: '/dashboard', title: 'Dashboard',  icon: 'dashboard', class: '' },

    { path: '/repair', title: 'Repair',  icon: 'build', class: '' },

    { path: '/customer', title: 'Customers',  icon:'group', class: '' },

    { path: '/user', title: 'User',  icon:'person', class: '' },
    { path: '/setting', title: 'Setting',  icon:'perm_data_setting', class: '' },
    
    // { path: '/upgrade', title: 'Upgrade to PRO',  icon:'unarchive', class: 'active-pro' },

];

@Component({
  selector: 'app-sidebar',
  templateUrl: './sidebar.component.html',
  styleUrls: ['./sidebar.component.css']
})
export class SidebarComponent implements OnInit {
  menuItems: any[];
  private username:string;
  constructor(private router:Router){}

  ngOnInit() {
    this.username =sessionStorage.getItem('FullName');
    this.menuItems = ROUTES.filter(menuItem => menuItem);
  }



  isMobileMenu() {
      if ($(window).width() > 991) {
          return false;
      }
      return true;
  };

  Logout(){
    sessionStorage.removeItem('UserID');
    sessionStorage.removeItem('FullName');
    sessionStorage.clear();
    this.router.navigate(['login']);
}
}
