import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { UserModel } from 'app/Models/user-model.model';
import { UserService } from 'app/Services/user.service';
import { AuthModel } from 'app/Models/auth-model.model';

import { HttpErrorResponse } from '@angular/common/http';
import { NotificationService } from 'app/Services/notification.service';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styles: []
})
export class LoginComponent implements OnInit {

  UserName: string;
  Password: string;
  constructor(private _router:Router,  private service: UserService, private echo: NotificationService) { }

  ngOnInit() {
  }

  Login(){
//     this.service.IsLoggedIn=true;

//     sessionStorage.setItem('UserName', 'Sumon khan');
// this._router.navigate(['dashboard'])
this.IsUserAvailable();
  }

  IsUserAvailable(){
   let User =new AuthModel();
   User.UserName = this.UserName;
   User.Password = this.Password;

    this.service.IsUserAvailable(User).subscribe(
      res => {
        let response= res as AuthModel;
        
        sessionStorage.setItem('UserID', response.UserID.toString());
        sessionStorage.setItem('FullName', response.FullName);
        sessionStorage.setItem('UserName', response.UserName);
        
        this._router.navigate(['dashboard'])


      },
      err => {
        // console.log(err);
        sessionStorage.setItem('UserID', "");
        let e = err as HttpErrorResponse;
        this.echo.Error(e.error.Value);
      },
    );
  }


}
