import { Injectable, Inject } from '@angular/core';
import { UserModel } from 'app/Models/user-model.model';
import {HttpClient, HttpResponse, HttpErrorResponse} from '@angular/common/http'
import { AuthModel } from 'app/Models/auth-model.model';


@Injectable({
  providedIn: 'root'
})
export class UserService {


  HostUrl: string;
public IsLoggedIn: boolean;

  constructor(private http: HttpClient, @Inject('BASE_URL') baseUrl: string) {
    this.HostUrl=baseUrl;

   }

   IsUserAvailable(user: AuthModel){

    return this.http.post(this.HostUrl + 'api/user/IsUserAvailable', user);
   }

  AddUpdateUser(uModel:UserModel) {
    return this.http.post(this.HostUrl + 'api/user/AddUpdateUser', uModel);
  }

  GetUserByName(name: string) {
    return this.http.get(this.HostUrl + 'api/user/GetUserByUserName/' + name);
  }

  IsUserNameUnique(name: string) {
    return this.http.get(this.HostUrl + 'api/user/IsUserNameUnique/' + name);
  }
}


